.bg-color-3 {
  background: #ebf7f6;
}
.feature-section {
  position: relative;
  padding: 120px 0px;
}

.feature-section .pattern-layer .pattern-1 {
  position: absolute;
  left: 80px;
  bottom: 0px;
  width: 299px;
  height: 142px;
  background-repeat: no-repeat;
}
.feature-section .pattern-layer .pattern-2 {
  position: absolute;
  right: 80px;
  top: 0px;
  width: 299px;
  height: 142px;
  background-repeat: no-repeat;
}

.feature-block-one .inner-box {
  position: relative;
  display: block;
  background: #fff;
  padding: 30px 30px 45px 30px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 30px #eaf2f1;
  transition: all 500ms ease;
}
.feature-block-one .inner-box .pattern .pattern-1 {
  position: absolute;
  left: -30px;
  top: 0px;
  width: 229px;
  height: 103px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: all 500ms ease;
}
.feature-block-one .inner-box .pattern .pattern-2 {
  position: absolute;
  right: 0px;
  bottom: 0px;
  width: 149px;
  height: 87px;
  opacity: 0;
  background-repeat: no-repeat;
  transition: all 500ms ease;
}
.feature-block-one .inner-box .icon-box {
  position: relative;
  min-height: 100px;
  margin-bottom: 5px;
}
.feature-block-one .inner-box .icon-box img {
  display: inline-block;
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}
.feature-block-one .inner-box p {
  color: #061a3a;
  margin-bottom: 0px;
}
.feature-block:first-child .feature-block-one .inner-box h3 {
  color: #fe5948;
}
.feature-block-one .inner-box h3 {
  font-size: 24px;
  line-height: 26px;
  font-weight: 900;
  margin-bottom: 25px;
}
.feature-block-one .inner-box .link a {
  color: #39cabb;
}
.feature-block-one .inner-box .link a {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 50px;
  line-height: 54px;
  text-align: center;
  font-size: 16px;
  background: #e6f5f3;
  border-radius: 50%;
  transform: scale(1, 1);
}
.feature-block-one .inner-box .btn-box {
  position: absolute;
  left: 0px;
  bottom: 45px;
  width: 100%;
  text-align: center;
}
.feature-block-one .inner-box .btn-box .theme-btn-one {
  padding: 12px 30px;
  font-size: 15px;
  font-weight: 600;
  transform: scale(0, 0);
}
.theme-btn-one {
  background: #39cabb;
}

.theme-btn-one:before {
  background: #453f85;
}

.theme-btn-one i {
  margin-inline: 10px;
}
.feature-block-one .inner-box:hover {
  box-shadow: 0 20px 30px #cee4e2;
}
.feature-block-one .inner-box:hover .btn-box .theme-btn-one {
  transform: scale(1, 1);
}

.feature-block-one .inner-box:hover .pattern .pattern-1 {
  left: 0;
  opacity: 1;
}

.feature-block-one .inner-box:hover .pattern .pattern-2 {
  opacity: 1;
}
.feature-block-one .inner-box h3 {
  color: #39cabb;
}
.feature-block:nth-child(3) .feature-block-one .inner-box h3 {
  color: #ffab01;
}
.feature-block:nth-child(4) .feature-block-one .inner-box h3 {
  color: #01b4d9;
}
